'use client';

import { useState, useRef, useEffect } from 'react';
import Link from 'next/link';

export default function ScanPage() {
  const [isScanning, setIsScanning] = useState(false);
  const [manualIsbn, setManualIsbn] = useState('');
  const [scannedBook, setScannedBook] = useState<any>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');
  const videoRef = useRef<HTMLVideoElement>(null);

  // Camera starten
  const startCamera = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({
        video: { facingMode: 'environment' }
      });
      if (videoRef.current) {
        videoRef.current.srcObject = stream;
        setIsScanning(true);
      }
    } catch (err) {
      setError('Camera toegang geweigerd. Gebruik de handmatige invoer.');
    }
  };

  // Camera stoppen
  const stopCamera = () => {
    if (videoRef.current?.srcObject) {
      const stream = videoRef.current.srcObject as MediaStream;
      stream.getTracks().forEach(track => track.stop());
      setIsScanning(false);
    }
  };

  // ISBN opzoeken via Open Library API
  const lookupIsbn = async (isbn: string) => {
    setIsLoading(true);
    setError('');
    setScannedBook(null);

    try {
      const cleanIsbn = isbn.replace(/[^0-9X]/gi, '');
      const response = await fetch(
        `https://openlibrary.org/api/books?bibkeys=ISBN:${cleanIsbn}&jscmd=data&format=json`
      );
      const data = await response.json();
      
      const bookData = data[`ISBN:${cleanIsbn}`];
      if (bookData) {
        setScannedBook({
          title: bookData.title,
          author: bookData.authors?.[0]?.name || 'Onbekende auteur',
          cover: bookData.cover?.medium || null,
          isbn: cleanIsbn,
          publishYear: bookData.publish_date,
          pages: bookData.number_of_pages,
        });
      } else {
        setError('Boek niet gevonden. Controleer het ISBN nummer.');
      }
    } catch (err) {
      setError('Er ging iets mis. Probeer het opnieuw.');
    } finally {
      setIsLoading(false);
    }
  };

  const handleManualSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (manualIsbn.trim()) {
      lookupIsbn(manualIsbn);
    }
  };

  const saveBook = (status: 'reading' | 'tbr' | 'finished') => {
    // Hier zou je de book data opslaan naar localStorage of een backend
    const savedBooks = JSON.parse(localStorage.getItem('shelfie-books') || '[]');
    savedBooks.push({ ...scannedBook, status, id: Date.now() });
    localStorage.setItem('shelfie-books', JSON.stringify(savedBooks));
    
    // Reset en ga naar library
    setScannedBook(null);
    setManualIsbn('');
    window.location.href = '/library';
  };

  useEffect(() => {
    return () => {
      stopCamera();
    };
  }, []);

  return (
    <main className="min-h-screen bg-surface-dark">
      {/* Background decorations */}
      <div className="fixed inset-0 overflow-hidden pointer-events-none">
        <div className="absolute top-1/4 left-1/4 w-96 h-96 bg-primary/10 rounded-full blur-[150px]" />
        <div className="absolute bottom-1/4 right-1/4 w-96 h-96 bg-secondary/10 rounded-full blur-[150px]" />
      </div>

      <div className="relative z-10">
        {/* Header */}
        <header className="sticky top-0 z-20 bg-surface-dark/80 backdrop-blur-lg border-b border-white/10">
          <div className="container mx-auto px-4 py-4">
            <div className="flex justify-between items-center">
              <Link href="/library" className="flex items-center gap-2 text-neutral-400 hover:text-white transition-colors">
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
                Back
              </Link>
              <h1 className="font-display font-bold text-lg text-white">Add Book</h1>
              <div className="w-16" /> {/* Spacer */}
            </div>
          </div>
        </header>

        <div className="container mx-auto px-4 py-8">
          {/* Als er een boek is gescand/gevonden */}
          {scannedBook ? (
            <div className="animate-scale-in">
              <div className="glass-card p-6 mb-6">
                <div className="flex gap-6">
                  {/* Cover */}
                  <div className="flex-shrink-0">
                    {scannedBook.cover ? (
                      <img
                        src={scannedBook.cover}
                        alt={scannedBook.title}
                        className="w-28 h-40 rounded-xl object-cover shadow-lg"
                      />
                    ) : (
                      <div className="w-28 h-40 rounded-xl bg-surface-light flex items-center justify-center">
                        <span className="text-4xl">ðŸ“š</span>
                      </div>
                    )}
                  </div>

                  {/* Info */}
                  <div className="flex-1">
                    <h2 className="text-white font-bold text-xl mb-1">{scannedBook.title}</h2>
                    <p className="text-neutral-400 mb-2">{scannedBook.author}</p>
                    {scannedBook.publishYear && (
                      <p className="text-neutral-500 text-sm mb-1">ðŸ“… {scannedBook.publishYear}</p>
                    )}
                    {scannedBook.pages && (
                      <p className="text-neutral-500 text-sm">ðŸ“„ {scannedBook.pages} pagina's</p>
                    )}
                    <p className="text-neutral-600 text-xs mt-2">ISBN: {scannedBook.isbn}</p>
                  </div>
                </div>
              </div>

              {/* Status selection */}
              <div className="space-y-3">
                <p className="text-neutral-400 text-center mb-4">Add to shelf as:</p>
                
                <button
                  onClick={() => saveBook('reading')}
                  className="w-full glass-card-hover p-4 flex items-center gap-4"
                >
                  <div className="w-12 h-12 rounded-xl bg-primary/20 flex items-center justify-center">
                    <span className="text-2xl">ðŸ“–</span>
                  </div>
                  <div className="text-left">
                    <p className="text-white font-semibold">Currently Reading</p>
                    <p className="text-neutral-400 text-sm">I'm reading this now</p>
                  </div>
                </button>

                <button
                  onClick={() => saveBook('tbr')}
                  className="w-full glass-card-hover p-4 flex items-center gap-4"
                >
                  <div className="w-12 h-12 rounded-xl bg-secondary/20 flex items-center justify-center">
                    <span className="text-2xl">ðŸ“š</span>
                  </div>
                  <div className="text-left">
                    <p className="text-white font-semibold">To Be Read (TBR)</p>
                    <p className="text-neutral-400 text-sm">On my reading list</p>
                  </div>
                </button>

                <button
                  onClick={() => saveBook('finished')}
                  className="w-full glass-card-hover p-4 flex items-center gap-4"
                >
                  <div className="w-12 h-12 rounded-xl bg-accent/20 flex items-center justify-center">
                    <span className="text-2xl">âœ…</span>
                  </div>
                  <div className="text-left">
                    <p className="text-white font-semibold">Finished</p>
                    <p className="text-neutral-400 text-sm">Already read this one</p>
                  </div>
                </button>
              </div>

              <button
                onClick={() => setScannedBook(null)}
                className="w-full mt-6 text-neutral-400 hover:text-white transition-colors py-3"
              >
                Scan another book
              </button>
            </div>
          ) : (
            <>
              {/* Scanner area */}
              <div className="glass-card p-6 mb-6">
                {isScanning ? (
                  <div className="relative">
                    <video
                      ref={videoRef}
                      autoPlay
                      playsInline
                      className="w-full h-64 rounded-xl object-cover"
                    />
                    {/* Scanner overlay */}
                    <div className="absolute inset-0 flex items-center justify-center">
                      <div className="w-48 h-32 border-2 border-primary rounded-lg relative">
                        <div className="absolute top-0 left-0 w-4 h-4 border-t-2 border-l-2 border-primary -translate-x-px -translate-y-px" />
                        <div className="absolute top-0 right-0 w-4 h-4 border-t-2 border-r-2 border-primary translate-x-px -translate-y-px" />
                        <div className="absolute bottom-0 left-0 w-4 h-4 border-b-2 border-l-2 border-primary -translate-x-px translate-y-px" />
                        <div className="absolute bottom-0 right-0 w-4 h-4 border-b-2 border-r-2 border-primary translate-x-px translate-y-px" />
                        {/* Scanning line animation */}
                        <div className="absolute left-2 right-2 h-0.5 bg-gradient-to-r from-transparent via-primary to-transparent animate-pulse" 
                             style={{ top: '50%' }} />
                      </div>
                    </div>
                    <button
                      onClick={stopCamera}
                      className="absolute top-4 right-4 bg-black/50 p-2 rounded-full text-white"
                    >
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                      </svg>
                    </button>
                  </div>
                ) : (
                  <button
                    onClick={startCamera}
                    className="w-full h-48 rounded-xl border-2 border-dashed border-white/20 flex flex-col items-center justify-center gap-4 hover:border-primary/50 hover:bg-white/5 transition-all duration-300"
                  >
                    <div className="w-16 h-16 rounded-2xl bg-gradient-to-br from-primary to-secondary flex items-center justify-center">
                      <svg className="w-8 h-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 9a2 2 0 012-2h.93a2 2 0 001.664-.89l.812-1.22A2 2 0 0110.07 4h3.86a2 2 0 011.664.89l.812 1.22A2 2 0 0018.07 7H19a2 2 0 012 2v9a2 2 0 01-2 2H5a2 2 0 01-2-2V9z" />
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 13a3 3 0 11-6 0 3 3 0 016 0z" />
                      </svg>
                    </div>
                    <div className="text-center">
                      <p className="text-white font-semibold">Scan ISBN Barcode</p>
                      <p className="text-neutral-400 text-sm">Tap to open camera</p>
                    </div>
                  </button>
                )}
              </div>

              {/* Divider */}
              <div className="flex items-center gap-4 my-6">
                <div className="flex-1 h-px bg-white/10" />
                <span className="text-neutral-500 text-sm">or enter manually</span>
                <div className="flex-1 h-px bg-white/10" />
              </div>

              {/* Manual input */}
              <form onSubmit={handleManualSubmit} className="space-y-4">
                <div>
                  <label htmlFor="isbn" className="block text-sm text-neutral-400 mb-2">
                    ISBN Number
                  </label>
                  <input
                    type="text"
                    id="isbn"
                    value={manualIsbn}
                    onChange={(e) => setManualIsbn(e.target.value)}
                    placeholder="978-0-123456-78-9"
                    className="input-modern"
                  />
                </div>

                {error && (
                  <div className="p-4 rounded-xl bg-red-500/10 border border-red-500/20 text-red-400 text-sm">
                    {error}
                  </div>
                )}

                <button
                  type="submit"
                  disabled={isLoading || !manualIsbn.trim()}
                  className="w-full btn-gradient py-4 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  <span className="flex items-center justify-center gap-2">
                    {isLoading ? (
                      <>
                        <svg className="w-5 h-5 animate-spin" fill="none" viewBox="0 0 24 24">
                          <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
                          <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" />
                        </svg>
                        Searching...
                      </>
                    ) : (
                      <>
                        <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                        </svg>
                        Look up book
                      </>
                    )}
                  </span>
                </button>
              </form>

              {/* Example ISBNs for testing */}
              <div className="mt-8 glass-card p-4">
                <p className="text-neutral-500 text-sm mb-3">Try these popular BookTok reads:</p>
                <div className="flex flex-wrap gap-2">
                  {[
                    { isbn: '9780062060624', title: 'Song of Achilles' },
                    { isbn: '9781649374042', title: 'Fourth Wing' },
                    { isbn: '9780316769174', title: 'Catcher in the Rye' },
                  ].map((book) => (
                    <button
                      key={book.isbn}
                      onClick={() => {
                        setManualIsbn(book.isbn);
                        lookupIsbn(book.isbn);
                      }}
                      className="px-3 py-1.5 rounded-full bg-white/5 text-neutral-400 text-sm hover:bg-primary/20 hover:text-primary-light transition-all duration-300"
                    >
                      {book.title}
                    </button>
                  ))}
                </div>
              </div>
            </>
          )}
        </div>

        {/* Bottom Navigation */}
        <nav className="fixed bottom-0 left-0 right-0 bg-surface-dark/90 backdrop-blur-lg border-t border-white/10">
          <div className="container mx-auto px-4">
            <div className="flex justify-around py-2">
              <Link href="/library" className="nav-item">
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.747 0 3.332.477 4.5 1.253v13C19.832 18.477 18.247 18 16.5 18c-1.746 0-3.332.477-4.5 1.253" />
                </svg>
                <span className="text-xs">Library</span>
              </Link>
              <Link href="/scan" className="nav-item active">
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v1m6 11h2m-6 0h-2v4m0-11v3m0 0h.01M12 12h4.01M16 20h2M4 12h2m14 0h2M6 20h2m-4-8h2m14 0h2" />
                </svg>
                <span className="text-xs">Scan</span>
              </Link>
              <Link href="/stats" className="nav-item">
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
                </svg>
                <span className="text-xs">Stats</span>
              </Link>
              <Link href="/profile" className="nav-item">
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                </svg>
                <span className="text-xs">Profile</span>
              </Link>
            </div>
          </div>
        </nav>
      </div>
    </main>
  );
}
